	export default{
		data(){
			return{
				adr:'',
				remark:'',
				flag:''
			}
		},
		onLoad(option) {
			this.flag=option.flag
		},
        onBackPress(option){
              plus.key.hideSoftKeybord()    
            },
		methods:{
			getAddress:function(e){
				this.adr=e.detail.value
			},
			getRemark:function(e){
				this.remark=e.detail.value
			},
			save:function(){
				if(this.adr==''){
					uni.showToast({
						title:'提币地址不能为空',
						icon:'none',
						duration:2000
					})
					return false
				}
				if(this.adr.length<=32){
					uni.showToast({
						title:'提币地址最少32位',
						icon:'none',
						duration:2000
					})
					return false
				}
				if(this.remark==""){
					uni.showToast({
						title:'备注名称不能为空',
						icon:'none',
						duration:2000
					})
					return false
				}
				uni.request({
					url:this.url+'walletaddress/',
					method:'POST',
					data:{
						wallet_key:this.remark,
						wallet_value:this.adr
					},
					header:{
						Authorization:'JWT'+' '+this.global_.token
					},
					success(res) {
						if(res.statusCode==202){
							uni.showToast({
								title:'地址格式不正确(只能是字母或数字)',
								icon:'none',
								duration:2000
							})
							return false
						}
						if(res.statusCode==204){
							uni.showToast({
								title:'昵称不可重复',
								icon:'none',
								duration:2000
							})
							return false
						}
						if(res.statusCode==205){
							uni.showToast({
								title:'地址不合法',
								icon:'none',
								duration:2000
							})
							return false
						}
						if(res.statusCode==200){
							uni.navigateBack({
								delta:1
							})
							uni.showToast({
								title:'添加成功',
								icon:'none',
								duration:1500
							})
						}
					}
				})
			}
		}
	}
